 
import './App.css';
import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import CardGroup from 'react-bootstrap/CardGroup'
import Card from 'react-bootstrap/Card'

import img1 from './Graphic/Blurple.webp'
import img2 from './Graphic/BeadDrip2.jpg'
import img3 from './Graphic/Hyundai.webp'
import logo from './image.svg';
import Navbar  from './components/navbar';
import Home  from './pages';
import Contact from './pages/contact';
import Portfolio from './pages/portfolio';
import Services from './pages/services';
 

function App() {

  return (
    
      <Router>
      <Navbar />
      <Switch>
        <Route path='/' exact component={Home}/>
        <Route path='/contact' component={Contact} />
        <Route path='/services' component={Services} />
        <Route path='/portfolio' component={Portfolio} />
         
      
      
        <header className="App-header">
        <h1 class="title"> Just Clean Auto Detailing </h1>
        <img src={logo} className="App-logo" alt="logo" />
        </header>
        </Switch>

        <body className="App-body">
        <CardGroup>
  <Card>
    <Card.Img variant="top" src={img1} />
    <Card.Body>
      <Card.Title> Paint correction</Card.Title>
      <Card.Text>
         Paint gets scratched, then dirt fills the scratches. Your paint is clouded. We can fix that and reveal new depths. 
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted"> </small>
    </Card.Footer>
  </Card>
  <Card>
    <Card.Img variant="top" src={img2} />
    <Card.Body>
      <Card.Title> Long term Ceramic Coating</Card.Title>
      <Card.Text>
        A strong, hydrophobic chemical coating layered on top of perfectly polished paint.  
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted"> </small>
    </Card.Footer>
  </Card>
  <Card>
    <Card.Img variant="top" src={img3} />
    <Card.Body>
      <Card.Title>Superior Washes</Card.Title>
      <Card.Text>
        Sometimes a car just needs a good hand wash. 
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted"> </small>
    </Card.Footer>
  </Card>
</CardGroup>
        </body>

    </Router>
    
  );
}

export default App;
