import { FaBars } from 'react-icons/fa';
import { NavLink as Link } from 'react-router-dom';
import styled from 'styled-components';
  
export const Nav = styled.nav`
 
margin-top: 0;
margin: auto;
text-align: center;
padding-top: 50px;
padding-bottom: 50px;
z-index: 1;
display: block;
margin-top: 0;
width: 100%;
min-width: auto;
height: 60px;
max-height: 60px;
font-size: 25px;
padding-top: 10px;
padding-bottom: 10px;
background: rgba(0,0,0,0.92);
font-size: 17px;
  /* Third Nav */
  /* justify-content: flex-start; */
`;
  
export const NavLink = styled(Link)`
  color: #d8d8d8;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
  cursor: pointer;
  &.active {
    color: #f1f1f1;
  }
`;
  
export const Bars = styled(FaBars)`
  display: none;
  color: #808080;
  @media screen and (max-width: 768px) {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 75%);
    font-size: 1.8rem;
    cursor: pointer;
  }
`;
  
export const NavMenu = styled.div`
  display: absolute;
  position: absolute;
  height: 50px;
  left: 50%;
  transform: translate(-50%, -25%);
  background: transparent ;
   

  
  /* Second Nav */
  /*  margin: auto; */
  display: flex;
  text-align: center;
  padding-top: 15px;
  padding-top: 15px;
  /* Third Nav */
  /* width: 100vw;
  white-space: nowrap; */
  @media screen and (max-width: 768px) {
    padding-top: 15px;
  padding-top: 15px;
  }
`;
  
export const NavBtn = styled.nav`
  display: flex;
  align-items: center;
  margin-right: 24px;
  /* Third Nav */
  /* justify-content: flex-end;
  width: 100vw; */
  @media screen and (max-width: 768px) {
    display: none;
  }
`;
  
export const NavBtnLink = styled(Link)`
  font-size: 25px;
  border-radius: 4px;
  background: #808080;
  padding: 10px 22px;
  color: #000000;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  /* Second Nav */
  margin-left: 24px;
  &:hover {
    transition: all 0.2s ease-in-out;
    background: #fff;
    color: #808080;
  }
`;