import React from 'react';
import {
    Nav,
    NavLink, 
    NavMenu,   
} from './navbarElements';

const Navbar = () => {
    return (
        <>
        <Nav>
            
                    <NavMenu>
                    <NavLink to='/index' activeStyle>
                            Home
                        </NavLink>
                        <NavLink to='/services' activeStyle>
                            Services
                        </NavLink>
                        <NavLink to='/portfolio' activeStyle>
                            Portfolio
                        </NavLink>
                        <NavLink to='/contact' activeStyle>
                            Contact
                        </NavLink>
                       
                    </NavMenu>
                               
        </Nav>
        
        </>
    );
};

export default Navbar;